"use client";

import React, { useState } from "react";
import RoomList from "./RoomList";
import RoomPostModal from "./RoomPostModal";

const RoomsPage: React.FC = () => {
  const [isPostModalOpen, setIsPostModalOpen] = useState(false);
  const [refreshList, setRefreshList] = useState(false);

  const openPostModal = () => setIsPostModalOpen(true);
  const closePostModal = () => setIsPostModalOpen(false);

  const handlePostSuccess = () => {
    setRefreshList((prev) => !prev);
  };

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold text-blue-700">Smart Room Finder</h1>
        <button
          onClick={openPostModal}
          className="bg-blue-700 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
        >
          Post Room
        </button>
      </div>
      <RoomList key={refreshList.toString()} />
      <RoomPostModal
        isOpen={isPostModalOpen}
        onClose={closePostModal}
        onPostSuccess={handlePostSuccess}
      />
    </div>
  );
};

export default RoomsPage;
