"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";
import RoomCard from "./RoomCard";

interface Room {
  id: string;
  title: string;
  description: string;
  rent: number;
  location: string;
  imageUrl?: string;
  viewCount: number;
}

const RoomList: React.FC = () => {
  const [rooms, setRooms] = useState<Room[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [maxRent, setMaxRent] = useState<number | "">("");
  const [city, setCity] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  const fetchRooms = async () => {
    setLoading(true);
    setError("");
    try {
      const params: any = { page };
      if (maxRent !== "") params.maxRent = maxRent;
      if (city) params.city = city;
      const response = await axios.get("/api/rooms", { params });
      setRooms(response.data.rooms);
      setTotalPages(response.data.totalPages);
    } catch (err) {
      setError("Failed to load rooms.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchRooms();
  }, [page]);

  const handleSearch = () => {
    setPage(1);
    fetchRooms();
  };

  return (
    <div>
      <div className="mb-6 flex flex-wrap gap-4 items-end">
        <div>
          <label className="block mb-1 font-semibold">Max Rent</label>
          <input
            type="number"
            value={maxRent}
            onChange={(e) => setMaxRent(e.target.value === "" ? "" : Number(e.target.value))}
            className="border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="e.g. 1000"
          />
        </div>
        <div>
          <label className="block mb-1 font-semibold">City</label>
          <input
            type="text"
            value={city}
            onChange={(e) => setCity(e.target.value)}
            className="border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="e.g. New York"
          />
        </div>
        <button
          onClick={handleSearch}
          className="bg-blue-700 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
        >
          Search
        </button>
      </div>

      {loading ? (
        <p>Loading rooms...</p>
      ) : error ? (
        <p className="text-red-600">{error}</p>
      ) : rooms.length === 0 ? (
        <p>No rooms found.</p>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
          {rooms.map((room) => (
            <RoomCard
              key={room.id}
              title={room.title}
              description={room.description}
              rent={room.rent}
              location={room.location}
              imageUrl={room.imageUrl}
              viewCount={room.viewCount}
            />
          ))}
        </div>
      )}

      <div className="mt-6 flex justify-center space-x-4">
        <button
          disabled={page <= 1}
          onClick={() => setPage((p) => Math.max(p - 1, 1))}
          className="px-4 py-2 bg-gray-300 rounded disabled:opacity-50"
        >
          Previous
        </button>
        <span className="px-4 py-2">
          Page {page} of {totalPages}
        </span>
        <button
          disabled={page >= totalPages}
          onClick={() => setPage((p) => Math.min(p + 1, totalPages))}
          className="px-4 py-2 bg-gray-300 rounded disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default RoomList;
