"use client";

import React, { useState } from "react";
import RoomsPage from "../components/Rooms/RoomsPage";
import ShopsPage from "../components/Shops/ShopsPage";
import MarketplacePage from "../components/Marketplace/MarketplacePage";
import Login from "../components/Auth/Login";
import Signup from "../components/Auth/Signup";
import Logout from "../components/Auth/Logout";
import Profile from "../components/Auth/Profile";

type Tab = "home" | "rooms" | "shops" | "marketplace" | "login" | "signup" | "profile" | "logout";

const HomePage: React.FC = () => {
  const [activeTab, setActiveTab] = useState<Tab>("home");

  const renderContent = () => {
    switch (activeTab) {
      case "rooms":
        return <RoomsPage />;
      case "shops":
        return <ShopsPage />;
      case "marketplace":
        return <MarketplacePage />;
      case "login":
        return <Login />;
      case "signup":
        return <Signup />;
      case "logout":
        return <Logout />;
      case "profile":
        return <Profile />;
      case "home":
      default:
        return (
          <div className="text-center mt-20">
            <h2 className="text-4xl font-bold text-blue-700 mb-4">Welcome to DIVI</h2>
            <p className="text-lg max-w-xl mx-auto text-gray-700">
              Your smart local platform for finding rooms, discovering shops, and buying/selling items.
            </p>
            <p className="mt-6 text-gray-600">
              Use the navigation above to explore Rooms, Shops, Marketplace, your Profile, or Admin panel.
            </p>
          </div>
        );
    }
  };

  return (
    <div>
      <nav className="flex justify-center space-x-6 py-4 bg-blue-100">
        <button
          onClick={() => setActiveTab("home")}
          className={`px-4 py-2 rounded ${
            activeTab === "home" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Home
        </button>
        <button
          onClick={() => setActiveTab("rooms")}
          className={`px-4 py-2 rounded ${
            activeTab === "rooms" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Rooms
        </button>
        <button
          onClick={() => setActiveTab("shops")}
          className={`px-4 py-2 rounded ${
            activeTab === "shops" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Shops
        </button>
        <button
          onClick={() => setActiveTab("marketplace")}
          className={`px-4 py-2 rounded ${
            activeTab === "marketplace" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Marketplace
        </button>
        <button
          onClick={() => setActiveTab("profile")}
          className={`px-4 py-2 rounded ${
            activeTab === "profile" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Profile
        </button>
        <button
          onClick={() => setActiveTab("login")}
          className={`px-4 py-2 rounded ${
            activeTab === "login" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Login
        </button>
        <button
          onClick={() => setActiveTab("signup")}
          className={`px-4 py-2 rounded ${
            activeTab === "signup" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Signup
        </button>
        <button
          onClick={() => setActiveTab("logout")}
          className={`px-4 py-2 rounded ${
            activeTab === "logout" ? "bg-blue-700 text-white" : "text-blue-700 hover:bg-blue-200"
          }`}
        >
          Logout
        </button>
      </nav>
      <main className="p-6">{renderContent()}</main>
    </div>
  );
};

export default HomePage;
