"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";
import ShopCard from "./ShopCard";

interface Shop {
  id: string;
  name: string;
  description: string;
  rating: number;
  location: string;
  imageUrl?: string;
}

const ShopList: React.FC = () => {
  const [shops, setShops] = useState<Shop[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  const fetchShops = async () => {
    setLoading(true);
    setError("");
    try {
      const params: any = { page };
      if (searchTerm) params.search = searchTerm;
      const response = await axios.get("/api/shops", { params });
      setShops(response.data.shops);
      setTotalPages(response.data.totalPages);
    } catch (err) {
      setError("Failed to load shops.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchShops();
  }, [page]);

  const handleSearch = () => {
    setPage(1);
    fetchShops();
  };

  return (
    <div>
      <div className="mb-6 flex flex-wrap gap-4 items-end">
        <div>
          <label className="block mb-1 font-semibold">Search Shops</label>
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="border border-gray-300 rounded px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="Enter shop name or service"
          />
        </div>
        <button
          onClick={handleSearch}
          className="bg-blue-700 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
        >
          Search
        </button>
      </div>

      {loading ? (
        <p>Loading shops...</p>
      ) : error ? (
        <p className="text-red-600">{error}</p>
      ) : shops.length === 0 ? (
        <p>No shops found.</p>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
          {shops.map((shop) => (
            <ShopCard
              key={shop.id}
              name={shop.name}
              description={shop.description}
              rating={shop.rating}
              location={shop.location}
              imageUrl={shop.imageUrl}
            />
          ))}
        </div>
      )}

      <div className="mt-6 flex justify-center space-x-4">
        <button
          disabled={page <= 1}
          onClick={() => setPage((p) => Math.max(p - 1, 1))}
          className="px-4 py-2 bg-gray-300 rounded disabled:opacity-50"
        >
          Previous
        </button>
        <span className="px-4 py-2">
          Page {page} of {totalPages}
        </span>
        <button
          disabled={page >= totalPages}
          onClick={() => setPage((p) => Math.min(p + 1, totalPages))}
          className="px-4 py-2 bg-gray-300 rounded disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default ShopList;
