"use client";

import React from "react";

interface RoomCardProps {
  title: string;
  description: string;
  rent: number;
  location: string;
  imageUrl?: string;
  viewCount: number;
  onClick?: () => void;
}

const RoomCard: React.FC<RoomCardProps> = ({
  title,
  description,
  rent,
  location,
  imageUrl,
  viewCount,
  onClick,
}) => {
  return (
    <div
      className="border rounded shadow hover:shadow-lg transition cursor-pointer overflow-hidden"
      onClick={onClick}
    >
      {imageUrl ? (
        <img src={imageUrl} alt={title} className="w-full h-48 object-cover" />
      ) : (
        <div className="w-full h-48 bg-gray-200 flex items-center justify-center text-gray-400">
          No Image
        </div>
      )}
      <div className="p-4">
        <h3 className="text-xl font-semibold mb-1">{title}</h3>
        <p className="text-gray-700 mb-2 line-clamp-2">{description}</p>
        <p className="font-bold text-blue-700 mb-1">${rent} / month</p>
        <p className="text-gray-600 text-sm mb-1">{location}</p>
        <p className="text-gray-500 text-xs">Views: {viewCount}</p>
      </div>
    </div>
  );
};

export default RoomCard;
