"use client";

import React, { useEffect } from "react";

const Logout: React.FC = () => {
  useEffect(() => {
    localStorage.removeItem("token");
    window.location.href = "/";
  }, []);

  return (
    <div className="text-center mt-20 text-gray-700">
      <p>Logging out...</p>
    </div>
  );
};

export default Logout;
