"use client";

import React from "react";

interface ItemCardProps {
  title: string;
  description: string;
  price: number;
  contactInfo: string;
  category: string;
  imageUrl?: string;
  onClick?: () => void;
}

const ItemCard: React.FC<ItemCardProps> = ({
  title,
  description,
  price,
  contactInfo,
  category,
  imageUrl,
  onClick,
}) => {
  return (
    <div
      className="border rounded shadow hover:shadow-lg transition cursor-pointer overflow-hidden"
      onClick={onClick}
    >
      {imageUrl ? (
        <img src={imageUrl} alt={title} className="w-full h-48 object-cover" />
      ) : (
        <div className="w-full h-48 bg-gray-200 flex items-center justify-center text-gray-400">
          No Image
        </div>
      )}
      <div className="p-4">
        <h3 className="text-xl font-semibold mb-1">{title}</h3>
        <p className="text-gray-700 mb-2 line-clamp-2">{description}</p>
        <p className="font-bold text-blue-700 mb-1">${price}</p>
        <p className="text-gray-600 text-sm mb-1">Category: {category}</p>
        <p className="text-gray-600 text-sm mb-1">Contact: {contactInfo}</p>
      </div>
    </div>
  );
};

export default ItemCard;
