"use client";

import React, { useEffect, useState } from "react";
import axios from "axios";

interface Listing {
  id: string;
  title: string;
  description: string;
  type: "room" | "shop" | "item";
  price?: number;
  location?: string;
}

const Profile: React.FC = () => {
  const [userEmail, setUserEmail] = useState<string | null>(null);
  const [listings, setListings] = useState<Listing[]>([]);
  const [error, setError] = useState("");

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      setError("You must be logged in to view your profile.");
      return;
    }
    // Decode token to get user email or fetch user info from API
    // For simplicity, assume token contains email in payload (JWT)
    try {
      const payload = JSON.parse(atob(token.split(".")[1]));
      setUserEmail(payload.email);
    } catch {
      setError("Invalid token.");
    }

    // Fetch user listings from API
    axios
      .get("/api/user/listings", { headers: { Authorization: `Bearer ${token}` } })
      .then((res) => setListings(res.data))
      .catch(() => setError("Failed to load your listings."));
  }, []);

  if (error) {
    return <p className="text-red-600 mt-6 text-center">{error}</p>;
  }

  return (
    <div className="max-w-4xl mx-auto mt-10">
      <h2 className="text-3xl font-bold text-blue-700 mb-6">Profile</h2>
      <p className="mb-4">Email: <strong>{userEmail}</strong></p>
      <h3 className="text-2xl font-semibold mb-4">Your Listings</h3>
      {listings.length === 0 ? (
        <p>You have no listings yet.</p>
      ) : (
        <ul className="space-y-4">
          {listings.map((listing) => (
            <li key={listing.id} className="border p-4 rounded shadow hover:shadow-lg transition">
              <h4 className="text-xl font-semibold">{listing.title}</h4>
              <p>{listing.description}</p>
              {listing.price !== undefined && <p>Price: ${listing.price}</p>}
              {listing.location && <p>Location: {listing.location}</p>}
              <p>Type: {listing.type}</p>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Profile;
