"use client";

import React from "react";

interface ShopCardProps {
  name: string;
  description: string;
  rating: number;
  location: string;
  imageUrl?: string;
  onClick?: () => void;
}

const ShopCard: React.FC<ShopCardProps> = ({
  name,
  description,
  rating,
  location,
  imageUrl,
  onClick,
}) => {
  const stars = Array.from({ length: 5 }, (_, i) => i < rating);

  return (
    <div
      className="border rounded shadow hover:shadow-lg transition cursor-pointer overflow-hidden"
      onClick={onClick}
    >
      {imageUrl ? (
        <img src={imageUrl} alt={name} className="w-full h-48 object-cover" />
      ) : (
        <div className="w-full h-48 bg-gray-200 flex items-center justify-center text-gray-400">
          No Image
        </div>
      )}
      <div className="p-4">
        <h3 className="text-xl font-semibold mb-1">{name}</h3>
        <p className="text-gray-700 mb-2 line-clamp-2">{description}</p>
        <div className="flex items-center mb-1">
          {stars.map((filled, idx) => (
            <span
              key={idx}
              className={`text-yellow-500 ${filled ? "opacity-100" : "opacity-30"}`}
            >
              ★
            </span>
          ))}
        </div>
        <p className="text-gray-600 text-sm mb-1">{location}</p>
      </div>
    </div>
  );
};

export default ShopCard;
